:root {
  --background: #121212;
  --background-card: #181818;
  --foreground: #f5f5f5;
  --foreground-muted: #b3b3b3;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
body {
  margin: 0;
  background-color: var(--background);
  color: var(--foreground);
}
.card {
  background-color: var(--background-card);
  border-radius: 0.75rem;
  border: 1px solid rgba(255, 255, 255, 0.08);
  padding: 1rem;
}
.header {
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  background-color: #000000;
}
.logo-nav-container {
  max-width: 960px;
  margin: 0 auto;
  padding: 0.75rem 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
.titulo-logo {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}
.logo-emoji {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 2rem;
  height: 2rem;
  border-radius: 0.5rem;
  background-color: rgba(16, 185, 129, 0.2);
}
.titulo-logo-Spotify {
  font-size: 0.9rem;
  font-weight: 600;
  margin: 0;
}
.texto-subtitulo {
  margin: 0;
  font-size: 0.7rem;
  color: var(--foreground-muted);
}
.nav-links {
  display: flex;
  align-items: center;
}
.nav-ul {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  list-style: none;
  margin: 0;
  padding: 0;
}
.panel-li {
  display: none;
}
@media (min-width: 640px) {
  .panel-li {
    display: block;
  }
}
.inicio,
.panel {
  font-size: 0.75rem;
  padding: 0.4rem 0.9rem;
  border-radius: 999px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  text-decoration: none;
  color: inherit;
  transition: background-color 0.15s ease, border-color 0.15s ease;
}
.inicio:hover,
.panel:hover {
  background-color: rgba(255, 255, 255, 0.08);
  border-color: rgba(255, 255, 255, 0.7);
}
.cerrar-sesion-button {
  font-size: 0.75rem;
  padding: 0.4rem 0.9rem;
  border-radius: 999px;
  border: none;
  cursor: pointer;
  background-color: #22c55e;
  color: #000;
  font-weight: 500;
  transition: background-color 0.15s ease;
}
.cerrar-sesion-button:hover {
  background-color: #4ade80;
}
.iniciar-sesion-texto {
  font-size: 0.7rem;
  color: var(--foreground-muted);
}
.portada-inicio {
  min-height: calc(100vh - 60px);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem 1rem;
}
.tarjeta-inicio {
  max-width: 640px;
  width: 100%;
  text-align: left;
}
.texto-pequeno {
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  color: var(--foreground-muted);
  margin-bottom: 0.5rem;
}
.titulo-inicio {
  font-size: 1.9rem;
  font-weight: 700;
  margin: 0 0 0.75rem 0;
}
.texto-descripcion {
  font-size: 0.9rem;
  margin: 0 0 1rem 0;
  color: var(--foreground-muted);
}
.lista-caracteristicas {
  list-style: none;
  padding: 0;
  margin: 0 0 1.5rem 0;
  font-size: 0.9rem;
}
.lista-caracteristicas li {
  margin-bottom: 0.35rem;
}
.boton-inicio {
  display: inline-block;
  padding: 0.6rem 1.4rem;
  border-radius: 999px;
  border: none;
  cursor: pointer;
  background-color: #22c55e;
  color: #000000;
  font-weight: 600;
  font-size: 0.95rem;
  margin-bottom: 0.75rem;
  transition: background-color 0.15s ease, transform 0.1s ease;
}
.boton-inicio:hover {
  background-color: #4ade80;
  transform: translateY(-1px);
}
.nota-inicio {
  font-size: 0.75rem;
  color: var(--foreground-muted);
  margin: 0;
}
.dashboard {
  padding: 2rem 1rem 3rem;
}
.dashboard-contenedor {
  max-width: 1100px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1.4fr 1.6fr;
  gap: 1.5rem;
}
@media (max-width: 900px) {
  .dashboard-contenedor {
    grid-template-columns: 1fr;
  }
}
.dashboard-columna-izquierda {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.dashboard-cabecera {
  margin-bottom: 0.5rem;
}
.dashboard-titulo {
  font-size: 1.3rem;
  font-weight: 600;
  margin: 0 0 0.3rem 0;
}
.dashboard-texto {
  margin: 0;
  font-size: 0.85rem;
  color: var(--foreground-muted);
}
.grid-widgets {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
}
@media (min-width: 720px) {
  .grid-widgets {
    grid-template-columns: 1fr 1fr;
  }
}
.widget-placeholder {
  min-height: 90px;
}
.widget-titulo {
  margin: 0 0 0.25rem 0;
  font-size: 0.9rem;
  font-weight: 600;
}
.widget-texto {
  margin: 0;
  font-size: 0.8rem;
  color: var(--foreground-muted);
}
.dashboard-columna-derecha {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.bloque-playlist {
  min-height: 260px;
}
.bloque-favoritos {
  min-height: 140px;
}
.bloque-cabecera {
  margin-bottom: 0.75rem;
}
.bloque-titulo {
  margin: 0 0 0.25rem 0;
  font-size: 1rem;
  font-weight: 600;
}
.bloque-subtitulo {
  margin: 0;
  font-size: 0.8rem;
  color: var(--foreground-muted);
}
.lista-vacia {
  font-size: 0.8rem;
  color: var(--foreground-muted);
}
.widget-generos {
  display: flex;
  flex-direction: column;
  gap: 0.6rem;
}
.widget-input {
  width: 100%;
  padding: 0.4rem 0.6rem;
  border-radius: 0.5rem;
  border: 1px solid rgba(255, 255, 255, 0.15);
  background-color: #111111;
  color: var(--foreground);
  font-size: 0.8rem;
}
.widget-input::placeholder {
  color: var(--foreground-muted);
}
.lista-generos {
  display: flex;
  flex-wrap: wrap;
  gap: 0.4rem;
  max-height: 150px;
  overflow-y: auto;
  padding-right: 0.2rem;
}
.boton-genero {
  padding: 0.35rem 0.7rem;
  font-size: 0.75rem;
  border-radius: 999px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  background-color: transparent;
  color: inherit;
  cursor: pointer;
  transition: background-color 0.15s ease, border-color 0.15s ease,
    transform 0.1s ease;
}
.boton-genero:hover {
  background-color: rgba(255, 255, 255, 0.08);
  border-color: rgba(255, 255, 255, 0.5);
}
.boton-genero-activo {
  background-color: #22c55e;
  border-color: #22c55e;
  color: #000000;
  transform: translateY(-1px);
}
.texto-seleccionados {
  font-size: 0.75rem;
  color: var(--foreground-muted);
  margin: 0;
}
@property --tw-border-style {
  syntax: "*";
  inherits: false;
  initial-value: solid;
}
@property --tw-font-weight {
  syntax: "*";
  inherits: false;
}
@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
@layer properties {
  @supports ((-webkit-hyphens: none) and (not (margin-trim: inline))) or ((-moz-orient: inline) and (not (color:rgb(from red r g b)))) {
    *, ::before, ::after, ::backdrop {
      --tw-border-style: solid;
      --tw-font-weight: initial;
    }
  }
}
